.playerList {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 50%;
  margin: auto;
}

@media (max-width:670px)  {
  .playerList {
    width: 100%;
  }
}

.general {
  margin-top: 20px;
}

.generalButton {
    background-color: #519250;
    border: none;
    color: white;
    margin: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 1.3em;
}

button {
  border-radius: 5px;
  padding: 16px;
  border: none;
  background-color: #138e43;
  font-weight: bold;
  color: white;
}


